<template>
  <div class="wrapper">
    <Navbar
      @setSearch="searchValue = $event"
    />
    <NotesList 
      :notes="filterNotes"
      @delNote="delNote"
      @change="change"
    />
    <Transition name="modal">
      <Modal 
        @close="isModalOpen = false" 
        v-if="isModalOpen"
        @addNote="addNote"
        :edit="edit"
        :editeNote="editeNote"
        @changeNote="changeNote"
      />
    </Transition>
    <button class="addnote" @click="isModalOpen = true, edit = false" v-if="!isModalOpen">
      <img src="@/assets/images/edit.svg" alt="">
    </button>
  </div>
</template>

<script>




import Modal from './components/Modal.vue';
import Navbar from './components/Navbar.vue';
import NotesList from './components/NotesList.vue';

  export default {
    components: {
    Navbar,
    NotesList,
    Modal
},
  data() {
    return {
      isModalOpen: false,
      notes: [],
      edit: false,
      editeNote: null,
      searchValue: ''
    }
  },
  mounted() {
    this.getNotes()
  },
  computed: {
    filterNotes() {
      return this.searchValue ? this.notes.filter(note => note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
    }
  },
  methods: {
    addNote(note) {
      this.notes.push(note)
    },
    delNote(note) {
     this.notes = this.notes.filter(item => item.id != note.id)
    },
    change(note) {
      this.isModalOpen = this.edit = true
      this.editeNote = note
    },
    changeNote(newNote) {
      this.editeNote = null
      this.notes.forEach((note,i) => note.id == newNote.id ? this.notes.splice(i,1, newNote) : '')
    },
    getNotes() {
      let localNotes = localStorage.notes
      if(localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    }
  },
  watch: {
   notes: {
      handler() {
       localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true
   }
  }
}
</script>

