<template>
    <div class="notes__item">
        <div class="notes__item-top" :class="{between: !turn}">
            <h2 class="notes__item-top-title">{{ note.title }}</h2>
            <p class="notes__item-top-date">{{ note.date }}</p>
        </div>
        <p class="notes__item-text">{{ note.text }}</p>
        <div class="notes__item-buttons">
            <button class="notes__item-buttons-btn purple" @click="$emit('change', note)">
                <img src="@/assets/images/edit.svg" alt="">
                <span>РЕДАКТИРОВАТЬ</span>
            </button>
            <button class="notes__item-buttons-btn red" @click="$emit('delNote', note)">
                <img src="@/assets/images/del.svg" alt="">
                <span>Удалить</span>
            </button>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            note: {
                typeof: Object
            },
            turn: {
                typeof: Boolean
            }
        }
    }
</script>
